import React, { useState, useEffect } from 'react';

const KanbanView = () => {
  const [workflows, setWorkflows] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Fetch Kanban data from the backend
  useEffect(() => {
    setLoading(true);
    fetch('/api/kanban')  // Replace this URL with your actual API endpoint
      .then(response => response.json())
      .then(data => {
        setWorkflows(data);  // Assuming the API returns an array of workflows
        setLoading(false);
      })
      .catch((err) => {
        setError(err);
        setLoading(false);
      });
  }, []);

  // Render the Kanban board
  return (
    <div>
      <h1>Kanban Board</h1>

      {/* Show loading indicator */}
      {loading && <p>Loading...</p>}

      {/* Show error message */}
      {error && <p>Error: {error.message}</p>}

      {/* Render the Kanban board if there is data */}
      {!loading && workflows.length > 0 && (
        <div className="kanban-board">
          {workflows.map((workflow) => (
            <div key={workflow.workflow_id} className="workflow">
              <h2>{workflow.workflow_name}</h2>
              <div className="workflow-steps">
                {workflow.steps.map((step) => (
                  <div key={step.step_id} className="kanban-step">
                    <h3>{step.step_name}</h3>
                    <ul>
                      {step.documents.map((document) => (
                        <li key={document.document_id}>
                          <strong>{document.title}</strong>
                          <p>{document.description}</p>
                        </li>
                      ))}
                    </ul>
                  </div>
                ))}
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Display a message if no data is available */}
      {!loading && workflows.length === 0 && (
        <p>No workflows available</p>
      )}
    </div>
  );
};

export default KanbanView;
